* {
  box-sizing: border-box;
}
/*шрифт примененный ко всему телу документа*/
body {
  margin: 0;
  font-family: 'PT Sans Narrow', sans-serif;
}
/*голова документа/бордюр*/
.header {
  width: 100%;
  height: 800px;
  background-color: #5d8fb4;
  background-image: url(componentos/assets/foto.jpg);
  background-position: top 20% center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
}
/*бордюр/про меня/мою работу с сылками на нижнюю часть документа*/
.blog {
  display: flex;
  flex-direction: column;
  margin-top: 40px;
  width: auto;
  justify-content: space-between;
  align-items: center;
  font-family: 'Marck Script', cursive;
}
@media (min-width: 768px) {
  .blog {
    margin-top: 75px;
    flex-direction: row;
    width: 750px;
  }
}

.blog-link {
  color: rgb(255, 255, 255);
  text-shadow: 1px 1px 2px black, 0 0 1em #537b90;
  text-decoration: none; /*без подчеркивания*/
  font-size: 26px;
}
@media (min-width: 768px) {
  .blog-link {
    font-size: 36px;
  }
}
/*центровка приветствия/имени*/
.logos {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
/*центровка текста в приветствии и имени*/
.hola {
  text-align: center;
  color: #fff;
  text-shadow: 1px 1px 2px black;
  font-family: 'Marck Script', cursive;
  margin-bottom: 5px;
  font-size: 30px;
}
@media (min-width: 768px) {
  .hola h1 {
    font-size: 56px;
  }
}
.slogan {
  text-align: center;
  font-size: 36px;
  color: #fff;
  margin-bottom: 60px;
}

/*мои соц сети и ссылки ниже*/
.logo {
  width: 200px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  color: #fff;
  font-size: 32px;
}
.logo-link {
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  color: #000;
  width: 50px;
  height: 50px;
  background-color: #fff;
  border-radius: 50%;
}


/*центровка тела документа */
.container {
  max-width: 1170px;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
  padding-top: 50px;
  padding-bottom: 50px;
}
@media (min-width: 768px) {
  .container {
    padding-left: 0;
    padding-right: 0;
    padding-top: 100px;
    padding-bottom: 100px;
  }
}
/*блог и фото по горизонтали*/
.container-blog {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .container-blog {
    flex-direction: row;
    justify-content: space-between;

  }
}
/*размеры фото*/
.blog-photo {
  max-width: 470px;
}
@media (min-width: 768px) {
  .blog-photo {
    padding-right: 15px;
    padding-left: 15px;
  }
}
.blog-photo-img {
  max-width: 100%;
  height: auto;
}
/*блог по горизонтали по отношению к фото*/
@media (min-width: 768px) {
  .blog-content {
    padding-right: 15px;
    width: 570px;
  }
}
.blog-content p {
  font-size: 20px;
  line-height: 1.4;
}
@media (min-width: 768px) {
  .blog-content p {
    font-size: 22px;

  }
}
/*стиль текста в блоге*/
.blog-title {
  color: #1b8ff1;
  font-size: 38px;
  font-weight: 700;
  margin-top: 0;
  margin-bottom: 20px;
  text-shadow: 1px 1px 2px black;
}
@media (min-width: 768px) {
  .blog-title {
    font-size: 58px;
  }
}

/* фон секции работа */
.container-work {
  background-color: #292929;
}
.mi-trabajo {
  text-align: center;
  font-size: 38px;
  color: #fff;
  margin-bottom: 15px;
}
@media (min-width: 768px) {
  .mi-trabajo {
    font-size: 58px;
    padding-bottom: 85px;
  }
}
/*все проекты по горизонтали*/
.work {
  display: flex;
  flex-direction: column;
  align-items: center;


}
@media (min-width: 768px) {
  .work {
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-around;
  }
}
@media (min-width: 1170px) {
  .work {
    justify-content: space-between;
  }
}
/*расстояние между проектами*/
.work-modelo {
  margin-bottom: 80px;
}
@media (min-width: 768px) {
  .work-modelo {
    margin-right: 10px;
  }
}
@media (min-width: 1130px) {
  .work-modelo {
    margin-bottom: 0;
  }
}

/*размер изобр проектов*/
.work-modelo-img img {
  width: 350px;
  height: 300px;
}

/*названия проектов*/
.work-modelo-lista h3 {
  margin-bottom: 15px;
  color: #fff;
  font-size: 26px;
}
@media (min-width: 768px) {
  .work-modelo-lista h3 {
    font-size: 36px;
  }
}
/*в классе work меняет цвет ссылок*/
.work-modelo-lista a {
  color: #a0d4fa;
}

.work-modelo-lista a:hover {
  color: #f2cd38;
}

.footer {
  display: flex;
  -webkit-box-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  align-items: center;
  font-family: FuturaNewLigh,sans-serif;
  border-top: 1px solid #d0d0d0;
  border-bottom: 1px solid #d0d0d0;
  height: 57px;
}
